.metronome {
  text-align: center;
  width: 800px;
  margin: 0 auto;
  padding: 30px;
}

.bpm-header {
  font-size: 32px;
}

.bpm-slider input {
  width: 100%;
  margin: 10px;
}

.metronome-container {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 768px) {
  .metronome-container {
    margin: 50% 50%;
  }

  .bpm-slider input {
    width: 50%;
  }
}
